import { Interval } from '../common';
import { SeverityConditionConfig } from '../common';
import { RuleCountThresholdConfig } from '../common';
import { RuleBasedSeverityConditionConfig } from '../common';



// DeviceScoreConfig is a set of parameters for device score config
export class DeviceScoreConfig {
 
    // device score alpha factor 
    public deviceScoreAlphaFactor: number;
 
    // occurrence threshold 
    public occurrenceThreshold: number;
 
    // ruleCount threshold 
    public ruleCountThreshold: number;
 
    // low weight alpha factor to use in device risk score calculation 
    public lowWeightAlphaFactor: number;
 
    // medium weight alpha factor to use in device risk score calculation 
    public mediumWeightAlphaFactor: number;
 
    // high weight alpha factor to use in device risk score calculation 
    public highWeightAlphaFactor: number;
 
    // high volume weight alpha factor to use in device risk score calculation 
    public highVolumeWeightAlphaFactor: number;
 
    // abnormal out ports alpha factor to use in device risk score calculation 
    public abnormalOutPortsAlphaFactor: number;
 
    // abnormal out endpoints alpha factor to use in device risk score calculation 
    public abnormalOutEndpointsAlphaFactor: number;
 
    // suspicious Ip alpha factor to use in device risk score calculation 
    public suspiciousIpAlphaFactor: number;
 
    // default range of the score for low severity 
    public defaultSeverityRange: Interval;
 
    // default severity factor 
    public defaultSeverityFactor: number;
 
    // 0 or more SeverityConditionConfig per severity 
    public severityConditionConfigs: SeverityConditionConfig[];
 
    // 0 or more RuleCountThresholdConfig 
    public ruleCountThresholdConfigs: RuleCountThresholdConfig[];
 
    // 0 or more RuleBasedSeverityConditionConfig 
    public ruleBasedSeverityConditionConfigs: RuleBasedSeverityConditionConfig[];
 
    // true if severity based , false if rule based 
    public isSeverityBased: boolean;
 
    // true if alert based , false if event based 
    public isAlertBased: boolean;
 
     constructor(deviceScoreAlphaFactor?: number, occurrenceThreshold?: number, ruleCountThreshold?: number, lowWeightAlphaFactor?: number, mediumWeightAlphaFactor?: number, highWeightAlphaFactor?: number, highVolumeWeightAlphaFactor?: number, abnormalOutPortsAlphaFactor?: number, abnormalOutEndpointsAlphaFactor?: number, suspiciousIpAlphaFactor?: number, defaultSeverityRange?: Interval, defaultSeverityFactor?: number, severityConditionConfigs?: SeverityConditionConfig[], ruleCountThresholdConfigs?: RuleCountThresholdConfig[], ruleBasedSeverityConditionConfigs?: RuleBasedSeverityConditionConfig[], isSeverityBased?: boolean, isAlertBased?: boolean) { 
        if (deviceScoreAlphaFactor !== undefined) { this.deviceScoreAlphaFactor = deviceScoreAlphaFactor; }
        if (occurrenceThreshold !== undefined) { this.occurrenceThreshold = occurrenceThreshold; }
        if (ruleCountThreshold !== undefined) { this.ruleCountThreshold = ruleCountThreshold; }
        if (lowWeightAlphaFactor !== undefined) { this.lowWeightAlphaFactor = lowWeightAlphaFactor; }
        if (mediumWeightAlphaFactor !== undefined) { this.mediumWeightAlphaFactor = mediumWeightAlphaFactor; }
        if (highWeightAlphaFactor !== undefined) { this.highWeightAlphaFactor = highWeightAlphaFactor; }
        if (highVolumeWeightAlphaFactor !== undefined) { this.highVolumeWeightAlphaFactor = highVolumeWeightAlphaFactor; }
        if (abnormalOutPortsAlphaFactor !== undefined) { this.abnormalOutPortsAlphaFactor = abnormalOutPortsAlphaFactor; }
        if (abnormalOutEndpointsAlphaFactor !== undefined) { this.abnormalOutEndpointsAlphaFactor = abnormalOutEndpointsAlphaFactor; }
        if (suspiciousIpAlphaFactor !== undefined) { this.suspiciousIpAlphaFactor = suspiciousIpAlphaFactor; }
        if (defaultSeverityRange !== undefined) { this.defaultSeverityRange = defaultSeverityRange; }
        if (defaultSeverityFactor !== undefined) { this.defaultSeverityFactor = defaultSeverityFactor; }
        if (severityConditionConfigs !== undefined) { this.severityConditionConfigs = severityConditionConfigs; }
        if (ruleCountThresholdConfigs !== undefined) { this.ruleCountThresholdConfigs = ruleCountThresholdConfigs; }
        if (ruleBasedSeverityConditionConfigs !== undefined) { this.ruleBasedSeverityConditionConfigs = ruleBasedSeverityConditionConfigs; }
        if (isSeverityBased !== undefined) { this.isSeverityBased = isSeverityBased; }
        if (isAlertBased !== undefined) { this.isAlertBased = isAlertBased; }
    }

}


