


// WGS-84 Geo Location
export class Location {
 
    // Latitude 
    public lat: number;
 
    // Longitude 
    public lon: number;
 
     constructor(lat?: number, lon?: number) { 
        if (lat !== undefined) { this.lat = lat; }
        if (lon !== undefined) { this.lon = lon; }
    }

}


