


// Tuple model represents a generic key-value pair
export class Tuple {
 
    // Generic key 
    public key: any;
 
    // Generic value 
    public value: any;
 
     constructor(key?: any, value?: any) { 
        if (key !== undefined) { this.key = key; }
        if (value !== undefined) { this.value = value; }
    }

}


