import { Injectable, Inject } from '@angular/core';
import { RestUtil, EntityResponse, EntitiesResponse, ActionResponse } from '../../utils';
import { PulseConfig } from '../../config';

import { ReportInstance } from '../entities';
import { ReportSourceCode } from '../enums';



// List of events related actions for the operator 
// @RequestHeader X-API-KEY The key to identify the application (console) 
// @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
@Injectable()
export class UsrReportsInstanceService {

  // URL to web api
  private baseUrl = '/reports-instance';

  // Class constructor
  constructor(@Inject('config') private config: PulseConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Update existing report instance in the system
   */
  update(body?: ReportInstance) {
    return this.rest.patch<EntityResponse<ReportInstance>>(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Delete a report instance from the system
   */
  delete(id?: string) {
    return this.rest.delete<ActionResponse>(`${this.baseUrl}/${id}`);
  }

  /**
   * Get single report instance by id
   */
  get(id?: string) {
    return this.rest.get<EntityResponse<ReportInstance>>(`${this.baseUrl}/${id}`);
  }

  /**
   * Find list of reports instance by query
   */
  find(accountId?: string, search?: string, sort?: string, page?: number, size?: number, from?: number, to?: number, reportTriggerSource?: ReportSourceCode[]) {
    const params = [];		
    if (accountId != null) { params.push(`accountId=${accountId}`); }
    if (search != null) { params.push(`search=${search}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (size != null) { params.push(`size=${size}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (reportTriggerSource != null) { params.push(`reportTriggerSource=${reportTriggerSource}`); }

		return this.rest.get<EntitiesResponse<ReportInstance>>(`${this.baseUrl}`, ...params);
  }

  /**
   * Download report file
   */
  downloadReport(id?: string) {
    return this.rest.download(`usr-reports-instance`,`${this.baseUrl}/${id}/download-report`);
  }

}
