
// Rule activity status code
export enum RuleActivityStatusCode {
 
    // Active [0] 
    ACTIVE = 0,
 
    // Inactive [1] 
    INACTIVE = 1,
 
}

// Return list of RuleActivityStatusCode values and their display names
export function GetRuleActivityStatusCodes() : Map<RuleActivityStatusCode, string> {

    let result = new Map<RuleActivityStatusCode, string>();

    
    result.set(RuleActivityStatusCode.ACTIVE, 'Active');
    
    result.set(RuleActivityStatusCode.INACTIVE, 'Inactive');
    

    return result;
}