import { DeviceTypeCode } from '../enums';
import { Location } from '../common';



// DeviceNode represents partial device data for map
export class DeviceNode {
 
    // Device id 
    public deviceId: string;
 
    // Device current security score 
    public score: number;
 
    // Device type: UNDEFINED | CONTROLLER | METER | SENSOR ... 
    public type: DeviceTypeCode;
 
    // WGS-84 Geo Location 
    public location: Location;
 
     constructor(deviceId?: string, score?: number, type?: DeviceTypeCode, location?: Location) { 
        if (deviceId !== undefined) { this.deviceId = deviceId; }
        if (score !== undefined) { this.score = score; }
        if (type !== undefined) { this.type = type; }
        if (location !== undefined) { this.location = location; }
    }

}


