import { DeviceReport } from '../entities';
import { ReportTypeCode } from '../enums';
import { BaseEntity } from '../base';
import { IntDistribution } from '../entities';



// Report instance entity represents generated report
export class ReportInstance extends BaseEntity {
 
    // 
    public accountId: string;
 
    // Start window time period of generated report [Epoch milliseconds Timestamp] 
    public from: number;
 
    // End window time period of generated report [Epoch milliseconds Timestamp] 
    public to: number;
 
    // Shield index score (1-100) 
    public score: number;
 
    // Devices distribution by status per report 
    public devicesDistributionByStatus: IntDistribution;
 
    // Device report 
    public deviceReport: DeviceReport;
 
    // Alerts distribution by severity 
    public alertsDistributionBySeverity: IntDistribution;
 
    // link to download report 
    public downloadLink: string;
 
    // report trigger source: Undefined, Ondemand or  Periodic 
    public reportTriggerSource: ReportTypeCode;
 
    // Username triggered the report or empty if system triggered 
    public triggeredByUser: string;
 
 
}


