import { DeviceNode } from '../common';
import { MapBounds } from '../common';



// DevicesMap is a data structure representing a list of device nodes and map bounds
export class DevicesMap {
 
    // List of device nodes 
    public deviceNodes: DeviceNode[];
 
    // Map bounds 
    public mapBounds: MapBounds;
 
     constructor(deviceNodes?: DeviceNode[], mapBounds?: MapBounds) { 
        if (deviceNodes !== undefined) { this.deviceNodes = deviceNodes; }
        if (mapBounds !== undefined) { this.mapBounds = mapBounds; }
    }

}


