


// Interval represents a finite int window (from - to)
export class Interval {
 
    // Start window interval 
    public from: number;
 
    // End window interval 
    public to: number;
 
     constructor(from?: number, to?: number) { 
        if (from !== undefined) { this.from = from; }
        if (to !== undefined) { this.to = to; }
    }

}


