import { SeverityTypeCode } from '../enums';
import { RuleCountThresholdConfig } from '../common';



// RuleBasedSeverityConditionConfig is a set of severity types code configs
export class RuleBasedSeverityConditionConfig {
 
    // Severity level: UNDEFINED | LOW | MEDIUM | HIGH | CRITICAL 
    public severityType: SeverityTypeCode;
 
    // bias config 
    public bias: number;
 
    // 0 or more RuleCountThresholdConfig 
    public ruleConditionThresholdConfig: RuleCountThresholdConfig[];
 
    // Minimum number of different rule types 
    public ruleTypesThreshold: number;
 
     constructor(severityType?: SeverityTypeCode, bias?: number, ruleConditionThresholdConfig?: RuleCountThresholdConfig[], ruleTypesThreshold?: number) { 
        if (severityType !== undefined) { this.severityType = severityType; }
        if (bias !== undefined) { this.bias = bias; }
        if (ruleConditionThresholdConfig !== undefined) { this.ruleConditionThresholdConfig = ruleConditionThresholdConfig; }
        if (ruleTypesThreshold !== undefined) { this.ruleTypesThreshold = ruleTypesThreshold; }
    }

}


