
// Report type code
export enum ReportTypeCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Compliance report [1] 
    COMPLIANCE = 1,
 
    // Devices report [2] 
    DEVICES = 2,
 
    // Events report [3] 
    EVENTS = 3,
 
}

// Return list of ReportTypeCode values and their display names
export function GetReportTypeCodes() : Map<ReportTypeCode, string> {

    let result = new Map<ReportTypeCode, string>();

    
    result.set(ReportTypeCode.UNDEFINED, 'Undefined');
    
    result.set(ReportTypeCode.COMPLIANCE, 'Compliance');
    
    result.set(ReportTypeCode.DEVICES, 'Devices');
    
    result.set(ReportTypeCode.EVENTS, 'Events');
    

    return result;
}