import { EventTypeCode } from '../enums';



// RuleCountThresholdConfig is a set of event type to threshold
export class RuleCountThresholdConfig {
 
    // event type code 
    public eventType: EventTypeCode;
 
    // occurrences threshold 
    public threshold: number;
 
     constructor(eventType?: EventTypeCode, threshold?: number) { 
        if (eventType !== undefined) { this.eventType = eventType; }
        if (threshold !== undefined) { this.threshold = threshold; }
    }

}


