import { Injectable, Inject } from '@angular/core';
import { RestUtil, EntityResponse, EntitiesResponse, ActionResponse } from '../../utils';
import { PulseConfig } from '../../config';

import { TimeSeriesOfFloat } from '../common';
import { IntDistribution } from '../entities';
import { Distribution } from '../entities';
import { Rule } from '../entities';
import { SeverityTypeCode } from '../enums';
import { RuleActivityStatusCode } from '../enums';
import { EventTypeCode } from '../enums';



// List of rules related actions 
// @RequestHeader X-API-KEY The key to identify the application (console) 
// @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
@Injectable()
export class UsrRulesService {

  // URL to web api
  private baseUrl = '/rules';

  // Class constructor
  constructor(@Inject('config') private config: PulseConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new rule
   */
  create(body?: Rule) {
    return this.rest.put<EntityResponse<Rule>>(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update existing rule in the system
   */
  update(body?: Rule) {
    return this.rest.patch<EntityResponse<Rule>>(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Delete a rule from the system
   */
  delete(id?: string) {
    return this.rest.delete<ActionResponse>(`${this.baseUrl}/${id}`);
  }

  /**
   * Get single rule by id
   */
  get(id?: string) {
    return this.rest.get<EntityResponse<Rule>>(`${this.baseUrl}/${id}`);
  }

  /**
   * Find list of rules by query
   */
  find(streamId?: string, search?: string, from?: number, to?: number, eventSeverity?: SeverityTypeCode[], activityStatus?: RuleActivityStatusCode[], eventType?: EventTypeCode[], includeSysRules?: boolean, sort?: string, page?: number, size?: number) {
    const params = [];		
    if (streamId != null) { params.push(`streamId=${streamId}`); }
    if (search != null) { params.push(`search=${search}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (eventSeverity != null) { params.push(`eventSeverity=${eventSeverity}`); }
    if (activityStatus != null) { params.push(`activityStatus=${activityStatus}`); }
    if (eventType != null) { params.push(`eventType=${eventType}`); }
    if (includeSysRules != null) { params.push(`includeSysRules=${includeSysRules}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (size != null) { params.push(`size=${size}`); }

		return this.rest.get<EntitiesResponse<Rule>>(`${this.baseUrl}`, ...params);
  }

  /**
   * Analyze rule configuration against historic data
   */
  analyze(from?: number, to?: number, interval?: number, size?: number, body?: Rule) {
    const params = [];		
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (interval != null) { params.push(`interval=${interval}`); }
    if (size != null) { params.push(`size=${size}`); }

		return this.rest.post<EntityResponse<TimeSeriesOfFloat>>(`${this.baseUrl}/analyze`, typeof body === 'object' ? JSON.stringify(body) : body, ...params);
  }

  /**
   * Find rule distribution by severity filtered by query
   */
  countBySeverity(streamId?: string, search?: string, from?: number, to?: number, eventSeverity?: SeverityTypeCode[], activityStatus?: RuleActivityStatusCode[], eventType?: EventTypeCode[], includeSysRules?: boolean, sort?: string, page?: number, size?: number) {
    const params = [];		
    if (streamId != null) { params.push(`streamId=${streamId}`); }
    if (search != null) { params.push(`search=${search}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (eventSeverity != null) { params.push(`eventSeverity=${eventSeverity}`); }
    if (activityStatus != null) { params.push(`activityStatus=${activityStatus}`); }
    if (eventType != null) { params.push(`eventType=${eventType}`); }
    if (includeSysRules != null) { params.push(`includeSysRules=${includeSysRules}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (size != null) { params.push(`size=${size}`); }

		return this.rest.get<EntityResponse<IntDistribution>>(`${this.baseUrl}/count/by-severity`, ...params);
  }

  /**
   * Find alerts count distribution by rule filtered by query
   */
  countAlertsByRule(streamId?: string, search?: string, from?: number, to?: number, eventSeverity?: SeverityTypeCode[], activityStatus?: RuleActivityStatusCode[], eventType?: EventTypeCode[], includeSysRules?: boolean, sort?: string, page?: number, size?: number) {
    const params = [];		
    if (streamId != null) { params.push(`streamId=${streamId}`); }
    if (search != null) { params.push(`search=${search}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (eventSeverity != null) { params.push(`eventSeverity=${eventSeverity}`); }
    if (activityStatus != null) { params.push(`activityStatus=${activityStatus}`); }
    if (eventType != null) { params.push(`eventType=${eventType}`); }
    if (includeSysRules != null) { params.push(`includeSysRules=${includeSysRules}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (size != null) { params.push(`size=${size}`); }

		return this.rest.get<EntityResponse<Distribution>>(`${this.baseUrl}/count-alerts/by-rule-id`, ...params);
  }

}
