import { Interval } from '../common';
import { SeverityTypeCode } from '../enums';



// SeverityConditionConfig is a set of severity configuration configs
export class SeverityConditionConfig {
 
    // Severity level: UNDEFINED | LOW | MEDIUM | HIGH | CRITICAL 
    public conditionSeverity: SeverityTypeCode;
 
    // List of severities type codes 
    public sevs: SeverityTypeCode[];
 
    // Count of occurrences 
    public countTH: number;
 
    // Severity factor 
    public severityFactor: number;
 
    // range of the score per severity 
    public severityRange: Interval;
 
     constructor(conditionSeverity?: SeverityTypeCode, sevs?: SeverityTypeCode[], countTH?: number, severityFactor?: number, severityRange?: Interval) { 
        if (conditionSeverity !== undefined) { this.conditionSeverity = conditionSeverity; }
        if (sevs !== undefined) { this.sevs = sevs; }
        if (countTH !== undefined) { this.countTH = countTH; }
        if (severityFactor !== undefined) { this.severityFactor = severityFactor; }
        if (severityRange !== undefined) { this.severityRange = severityRange; }
    }

}


