import { Location } from '../common';



// MapBounds represents maps bounds using 2 points southwest and northeast
export class MapBounds {
 
    // WGS-84 Geo Location 
    public southwest: Location;
 
    // WGS-84 Geo Location 
    public northeast: Location;
 
     constructor(southwest?: Location, northeast?: Location) { 
        if (southwest !== undefined) { this.southwest = southwest; }
        if (northeast !== undefined) { this.northeast = northeast; }
    }

}


